<template>
  <div class="staticReports ">
    <el-container>
      <el-header>
        <div class="staticReports loadingtext minWidth"
            v-loading="imgLoading"
            element-loading-text="加载中..."
            element-loading-background="rgba(255, 255, 255, 0.8)">
          <div class="taticReports-content">
            <el-row type="flex" class="staticReports-header" justify="space-between" >
              <el-col :span="17" class="titleMT"><span class="mz" @click="$router.back(-1)">流程报表</span><i :class="icon"></i>
              </el-col>
              <el-col :span="6" class="titleBtn clearfix">
                <el-button round @click="refresh" type="primary"   size="mini">重置</el-button>
                <el-button round @click="search" type="primary"  size="mini">查询</el-button>
              </el-col>
            </el-row>
            <br>
              <div class="statement reportCondition">
                <span class="fz16">动态-流程-流程退回率统计</span>
                <el-row  class="staticReports-middle lh35 staticReportsMiddle" >
                  <el-col :span="2" :sm="4" :md="1" class="classSelect"><span>流程分类选择</span></el-col>
                  <el-col :span="7" class="classSelectMR process-inputbox">
                    <i class="iconfont icon-organization" @click="openDailog('processClassification')" ></i>
                    <span @dblclick="openDailog('processClassification')">
                  <el-input v-model="processCategoryName" placeholder="" :disabled="true" :title="processCategoryName"></el-input>
                  </span>
                  </el-col>
                  <!--<el-col :span="1"> <i class="iconfont icon-organization" @click="openDailog('processClassification')" ></i></el-col>-->
                  <el-col  :span="1"><span>&nbsp;</span></el-col>
                  <el-col :span="2" :sm="4" :md="1" class="processSelect"><span>流程选择</span></el-col>
                  <el-col :span="6" class="process-inputbox">
                    <i class="iconfont icon-organization" @click="openDailog('processSelection')" ></i>
                    <span @dblclick="openDailog('processSelection')">
                  <el-input v-model="processName" placeholder="" :disabled="true" :title="processName"></el-input>
                  </span>
                  </el-col>
                   <!--<i class="iconfont icon-organization" @click="openDailog('processSelection')" ></i>-->
                </el-row>
                <el-row  class="reportCondition-date reportConditionDate">
                  <el-col :span="2" :sm="4" :md="1" class="timeDuan"><span>时间段</span></el-col>
                  <el-col :span="8" :sm="8" class="timeSelect">
                    <el-radio-group v-model="selectValue" @change="change">
                      <el-radio :label="1">本周</el-radio>
                      <el-radio :label="2">本月</el-radio>
                      <el-radio :label="3">本季度</el-radio>
                      <el-radio :label="4">本年</el-radio>
                      <el-radio :label="5">其他</el-radio>
                    </el-radio-group>
                  </el-col>
                  <el-col :span="2" :sm="4" :md="1" class="space"><span>&nbsp;</span></el-col>
                  <el-col :span="7" :sm="6" v-if="selectValue === 5 ">
                    <div class="border">
                      <el-date-picker
                        class="timeWidth"
                        v-model="model.Date"
                        type="daterange"
                        value-format="yyyy-MM-dd"
                        range-separator="~"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                      </el-date-picker>
                    </div>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="4" class="buttonWrap buttonWrapTA" >
                    <el-button round @click="exportList" type="primary"  size="mini"><i class="iconfont icon-export"></i>导出</el-button>
                  </el-col>
                </el-row>
              </div>
            <div class="statement ">
              <el-table
                ref="singleTable"
                :data="tableData"
                :height="isShow?fullHeight-320:fullHeight-215"
                @sort-change="sortChange"
                highlight-current-row
                border
                class="tableWidth">
                <el-table-column
                  prop="processID"
                  width="200"
                  sortable='custom'
                  label="ID">
                </el-table-column>

                <el-table-column
                  prop="processName"
                  sortable='custom'
                  align="left"
                  label="流程名称">
                </el-table-column>

                <el-table-column
                  prop="processFullName"
                  sortable='custom'
                  align="left"
                  label="流程路径">

                  <!--<template slot-scope="scope">-->
                  <!--<div class="table-popover" :title="scope.row.processFullName">{{scope.row.processFullName}}</div>-->
                  <!--</template>-->
                </el-table-column>
                <el-table-column
                  prop="processTotalCnt"
                  sortable='custom'
                  label="处理实例数">
                </el-table-column>
                <el-table-column
                  prop="returnCnt"
                  sortable='custom'
                  label="退回实例数">
                </el-table-column>
                <el-table-column
                  prop="returnRate"
                  sortable='custom'
                  label="退回率" width="200">
                </el-table-column>
                <div slot="empty" v-show="!imgLoading">
                  <p :style="{'marginTop': '23px'}">暂无数据</p>
                </div>
                <div slot="append">
                  <!--分页-->
                  <page :pageIndex="pageIndex" :total="model.selectForm.total" :pageSize="pageSize" :pageSizesList="pageSizesList"
                    @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></page>
                </div>
              </el-table>
              

            </div>

          </div >
        </div>
        <!--弹窗组件-->
        <component :is="componentParam.componentType" v-if="showdialog"
                   @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild"
                   v-bind:componentParam="componentParam"></component>

      </el-header>
    </el-container>
  </div>
</template>

<script>
  import processClassification from '../components/dailog/processClassification.vue'
  import processSelection from '../components/dailog/processSelection.vue'
  import util from '../js/util'
  import orginatordialog from '../components/dailog/originator_dailog'
  import projectdialog from '../components/dailog/project_dialog'
  import processdialog from '../components/dailog/process_dialog'
  import moment from 'moment'
  import loading from '../plugins/loading/loading'
  import page from "../plugins/page/index";
  /*本周*/
  var firstDay = moment().day(0).format('YYYY-MM-DD');
  var lastDay = moment().day(6).format('YYYY-MM-DD');
  /*本月*/
  var firstMonth = moment().startOf('month').format('YYYY-MM-DD');
  var lastMonth  = moment().endOf('month').format('YYYY-MM-DD');
  /*本季度*/
  var firstSeason = moment().startOf('quarter').format('YYYY-MM-DD');
  var lastSeason  = moment().endOf('quarter').format('YYYY-MM-DD');
  /*本年*/
  var firstYear = moment().startOf('year').format('YYYY-MM-DD');
  var lastYear  = moment().endOf('year').format('YYYY-MM-DD');
  export default {
    name: '',
    components: {
      page,
      'processClassification': processClassification, //选择流程分类
      'processSelection':processSelection             //选择流程
    },
    mounted() {
      // var that = this;
      // that.getList();
    },
    data() {
      return {
        fullHeight: document.documentElement.clientHeight,//获取可见文档高度
        organSelectRadio: '', // 以“人”统计退回率组织选择下 单选框
        isShow:true,  //头部内容是状态
        isShowReportCondition: true,//table内容状态
        arrt:'收起',//头部文字
        arrtReportCondition: '收起',//table内容文字
        icon:'el-icon-arrow-down', //图标
        selectValue: 1,//定义初始Radio状态
        tableData: [],  //table
        startDate:'',//第一个日期
        finishDate:'', //第二个日期
        childDataUrl:{url:''},//传给子组件的值
        comp:{},//定义弹窗空对象
        showdialog: false,//控制弹窗打开关闭
        pageSizesList:[10,20,50,100],
        model: {
          selectForm: {
            processIDs:'',//流程ID
            processFullName:'',//流程名称
          },
          Date:[]
        },
        componentParam:{
          //弹窗类型
          componentType:{
            type: String,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData:{
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck:{
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow:{
            type: Boolean,
            required: true
          },
        },
        processName:'',//显示文本
        processIDs:'',//查询条件
        projectList:[],//子向父组件传的全部数据
        processList:[],
        OrgId:'',
        type: 1,
        pageIndex:1,
        pageSize:10,
        total:0,
        orderBy: '',
        exportData:[],

        processCategoryName:'',//流程分类名
        processCategoryIDs:'',//流程分类id
        processCategoryList:[],//流程分类选中数据
        // 加载动画
        imgLoading: false
      }

    },
    methods:{

      change(){
        switch (this.selectValue) {
          case 1:
            this.startDate = firstDay;
            this.endDate = lastDay;
            break;
          case 2:
            this.startDate = firstMonth;
            this.endDate = lastMonth;
            break;
          case 3:
            this.startDate = firstSeason;
            this.endDate = lastSeason;
            break;
          case 4:
            this.startDate = firstYear;
            this.endDate = lastYear;
            break;
          case 5:
            this.startDate = this.model.Date[0] ? `${this.model.Date[0]} 00:00:00` : '';
            this.endDate = this.model.Date[1] ? `${this.model.Date[1]} 23:59:59` : '';
            break;
        }

      },
      /*
          * 头部展开动画
          * */

      showToggle(){
        this.isShow = !this.isShow;
        if(this.isShow){
          this.arrt="收起";
          this.icon="el-icon-arrow-up"
        }else {
          this.arrt="展开";
          this.icon="el-icon-arrow-down"
        }
      },

      openDailog(type) {
        //控制弹窗头部标题显示隐藏(默认显示)
        this.componentParam.closeShow = false;
        // 弹出框类型
        this.componentParam.componentType = type;
        //是否单选
        this.componentParam.oneCheck = false;
        //设置回填数据
        if(type == 'processClassification'){
          this.componentParam.backfillData = this.processCategoryList;
        }else  if(type == 'processSelection'){
          this.componentParam.backfillData = this.processList;
        }
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
      },

      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
       //流程分类 选择
        if (data.type && data.type == "processClassification") {
          this.processCategoryList= data.list;
          let  processCategoryIDs = [];
          let  processCategoryName = [];
          for(let node of data.list){
            processCategoryName.push(node.name);
            processCategoryIDs.push(node.id+'');
          }
          this.processCategoryIDs = processCategoryIDs.join(',');
          this.processCategoryName = processCategoryName.join('；');
        }else if(data.type && data.type == "processSelection"){
          //流程 选择
          this.processList = data.list;
          let processId = [];
          let processName = [];
          for(let node of data.list){
            processName.push(node.name);
            processId.push(node.processId+'');
          }
          this.processIDs = processId.join(',');
          this.processName = processName.join('；');
        }
        this.hideComponent();
      },

      //获取所有 processId
      getProcessID(nodes,process){
        for(let node of nodes){
          if(node.Child){
            this.getProcessID(node.Child,process);
          }else{
            if(node.processId && node.processId!=='0' && node.processId!==null){
              process.processId.push(node.processId+'');
            }else{
              process.processCategoryIDs.push(node.id+'');
            }
            return;
          }
          process.processName.push(node.name.trim());
        }
      },
      handleSizeChange(val) {
        var pageSize = `${val}`;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        this.getList()
      },
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getList();
      },

      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;         util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
      sortChange(column){
        this.orderBy = `${(column.prop=='statusName')?'status':column.prop} ${column.order.replace('ending','')}`;
        this.getList();
      },

      renderIcon(create, {column}) {
        return create("i", {"class": "iconfont icon-process"});
      },
      /*
      * 页面拉取数据
      **/

      getList(){
        if (this.processIDs.length === 0 && this.processCategoryIDs.length === 0) {
          this.$message.warning('请选择流程分类或流程选择进行查询！');
          return;
        }
        // loading.startLoading();
        this.imgLoading = true;
        var that=this;
        that.change();
        that.model.Date==null?that.model.Date=[]:'';

        let obj = {
          startDate:that.startDate,
          endDate:that.endDate,
          processIDs:this.processIDs,
          processCategoryIDs:this.processCategoryIDs,
          selectValue:that.selectValue,
          pageIndex:that.pageIndex-1,
          pageSize:that.pageSize,
          orderBy:this.orderBy
        };
        if(that.selectValue==5 && that.startDate=='' && that.endDate=='') {
          that.$message.warning('请选择日期');
          // loading.endLoading();
          this.imgLoading = false;
          return
        }
        that.$axios.post('/api/reportForm/getHighestDynamicFlowBack',obj).then((result)=>{
          that.tableData=result.data.processList;
          that.model.selectForm.total=result.data.listCounts;
          for(let i=0;i<result.data.processList.length;i++){
            result.data.processList[i].returnRate= result.data.processList[i].returnRate+'%'
          }
          // loading.endLoading()
          this.imgLoading = false;
        }).catch(()=>{
          // loading.endLoading();
          this.imgLoading = false;
          that.$message.error('查询失败')
        })
      },

      //新导出
      exportList() {
        var that = this;
        that.change();
        loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        var data ={
          tableName:'动态-流程-退回率最高的流程',
          params:{
            startDate:that.startDate,
            endDate:that.endDate,
            processIDs: that.model.selectForm.processIDs,
            processCategoryIDs:that.processCategoryIDs,
            selectValue:that.selectValue,
            pageIndex:0,
            pageSize:1000,//that.model.selectForm.total,
            orderBy:this.orderBy
          }
        }
        that.model.Date==null?that.model.Date=[]:'';
        that.model.selectForm.startDate = that.model.Date[0] ? `${that.model.Date[0]} 00:00:00` : '';
        that.model.selectForm.endDate = that.model.Date[1] ? `${that.model.Date[1]} 23:59:59` : '';
        this.$axios.post('/api/exportExcelForTable',data,{responseType: 'blob'}).then((result) => {
          const content = result.data;
          for(var i=0;i<result.data.length;i++){
            result.data.processList[i].returnRate= result.data.processList[i].returnRate+'%'
          }
          const dowLoadFileName = '动态-流程-退回率最高的流程TOP10';
          util.exportSearchList(dowLoadFileName, content);
          loading.endLoading();
        }).catch((err) => {
          loading.endLoading();
          this.$message.error(`导出Excel失败!ERROR MESSAGE:${err}`);
        });
      },


      /*
      * 查询方法
      * */
      search(){
        this.getList()
      },
      /*
       * 页面重置
      **/
      refresh(){
        this.componentParam.backfillData = new Array();
        this.processList = new Array();
        this.processCategoryList = new Array();
        this.$nextTick(() => {
          this.processName='';//流程类别
          this.processIDs='';//流程ID
          this.selectValue= 1;
          this.model.Date = '';//日期
          this.startDate = firstDay;
          this.endDate = lastDay;
          this.processCategoryName='';
          this.processCategoryIDs='';

        });
      },


    },

    watch: {
      fullHeight () {
        return document.documentElement.clientHeight;
      }
    },
    computed: {}
  }
</script>
<style lang="scss" scoped>

</style>





